import * as React from "react";
import { Link, HeadFC, PageProps } from "gatsby";

const styles = {
  container: {
    backgroundColor: "#FFD700", // Bravo Yellow
    color: "#1A1A1A", // Dark text
    padding: "96px 24px",
    fontFamily: "-apple-system, Roboto, sans-serif, serif",
    textAlign: "center" as const,
    minHeight: "100vh",
  },
  heading: {
    marginTop: 0,
    marginBottom: "32px",
    fontSize: "2.75rem",
    fontWeight: 700,
  },
  paragraph: {
    marginBottom: "24px",
    fontSize: "1.25rem",
  },
  code: {
    color: "#1A1A1A",
    padding: "4px 8px",
    backgroundColor: "#FFFFFF", // White background
    fontSize: "1rem",
    borderRadius: "4px",
    fontFamily: "monospace",
  },
  link: {
    display: "inline-block",
    marginTop: "20px",
    color: "#1A1A1A",
    backgroundColor: "#FFFFFF",
    padding: "10px 20px",
    borderRadius: "6px",
    textDecoration: "none",
    fontWeight: 600,
    fontSize: "1rem",
    transition: "all 0.2s ease-in-out",
  },
};

export const NotFoundPage: React.FC<PageProps> = () => {
  return (
    <main style={styles.container}>
      <h1 style={styles.heading}>404 - Page Not Found</h1>
      <p style={styles.paragraph}>
        Sorry üòî, we couldn‚Äôt find what you were looking for.
        {process.env.NODE_ENV === "development" && (
          <>
            <br />
            Try creating a page in <code style={styles.code}>src/pages/</code>.
          </>
        )}
      </p>
      <Link to="/" style={styles.link}>
        ‚Üê Go back home
      </Link>
    </main>
  );
};

export default NotFoundPage;

export const Head: HeadFC = () => <title>Page Not Found | Bravo Roofing</title>;
